import moment from 'moment';

export default function calculateBirthdays(dateOfBirth) {
  const dates = [];
  const oneDay = 1000 * 60 * 60 * 24;
  let start1 = new Date(dateOfBirth.getFullYear(), 0, 0);
  const diff1 = dateOfBirth - start1;
  const BdayInNum = Math.floor(diff1 / oneDay);
  const today = new Date();
  const start = new Date(today.getFullYear(), 0, 0);
  const diff = today - start;
  const todayInNum = Math.floor(diff / oneDay);

  const daysDiff = todayInNum - BdayInNum;
  let numBeforeBday = Math.floor(daysDiff / 52) + 1;

  let isLeapYear = false;
  let i = 0;

  while (i < 7) {
    let dogRealBday = new Date();
    if (isLeapYear) {
      dogRealBday = dogRealBday.setTime(
        dateOfBirth.getTime() + numBeforeBday * (366 / 7) * 86400000
      );
    } else {
      dogRealBday = dogRealBday.setTime(
        dateOfBirth.getTime() + numBeforeBday * (365 / 7) * 86400000
      );
    }
    const dateStore = new Date(dogRealBday);
    if (dateStore.getFullYear() % 4 === 0) {
      isLeapYear = true;
    } else {
      isLeapYear = false;
    }
    let dateformated = moment(dateStore).format('MMMM Do');
    dates.push(dateformated);
    numBeforeBday++;
    i++;
  }
  return dates;
}
